import React, { useState, useEffect } from 'react';

const CARD_SYMBOLS = ['üçé', 'üçå', 'üçí', 'üçì', 'üçä', 'üçã', 'üçâ', 'üçá'];

const MemoryMatch = () => {
  const [cards, setCards] = useState([]);
  const [flipped, setFlipped] = useState([]);
  const [solved, setSolved] = useState([]);
  const [moves, setMoves] = useState(0);

  useEffect(() => {
    initializeGame();
  }, []);

  const initializeGame = () => {
    const shuffledCards = [...CARD_SYMBOLS, ...CARD_SYMBOLS]
      .sort(() => Math.random() - 0.5)
      .map((symbol, index) => ({ id: index, symbol }));
    setCards(shuffledCards);
    setFlipped([]);
    setSolved([]);
    setMoves(0);
  };

  const handleCardClick = (id) => {
    if (flipped.length === 2 || flipped.includes(id) || solved.includes(id)) return;
    
    const newFlipped = [...flipped, id];
    setFlipped(newFlipped);
    setMoves(moves + 1);

    if (newFlipped.length === 2) {
      const [firstId, secondId] = newFlipped;
      if (cards[firstId].symbol === cards[secondId].symbol) {
        setSolved([...solved, firstId, secondId]);
      }
      setTimeout(() => setFlipped([]), 1000);
    }
  };

  const isGameOver = solved.length === cards.length;

  return (
    <div className="memory-match">
      <h2>Memory Match</h2>
      <div className="game-info">
        <p>Moves: {moves}</p>
        {isGameOver && <p>Congratulations! You won in {moves} moves!</p>}
      </div>
      <div className="card-grid">
        {cards.map(({ id, symbol }) => (
          <div
            key={id}
            className={`card ${flipped.includes(id) || solved.includes(id) ? 'flipped' : ''}`}
            onClick={() => handleCardClick(id)}
          >
            {flipped.includes(id) || solved.includes(id) ? symbol : '?'}
          </div>
        ))}
      </div>
      <button onClick={initializeGame} className="restart-button">
        {isGameOver ? 'Play Again' : 'Restart Game'}
      </button>
    </div>
  );
};

export default MemoryMatch;