import React, { useState } from 'react';
import './App.css';
import SnakeGame from './SnakeGame';
import WhackAMole from './WhackAMole';
import MemoryMatch from './MemoryMatch';

const GameCard = ({ title, description, onClick }) => (
  <div className="game-card" onClick={onClick}>
    <h2>{title}</h2>
    <p>{description}</p>
  </div>
);

const GameRequestForm = ({ onSubmit }) => {
  const [gameName, setGameName] = useState('');
  const [gameDescription, setGameDescription] = useState('');

  const handleSubmit = (e) => {
    e.preventDefault();
    onSubmit({ name: gameName, description: gameDescription });
    setGameName('');
    setGameDescription('');
  };

  return (
    <form className="game-request-form" onSubmit={handleSubmit}>
      <h3>Request a New Game</h3>
      <input
        type="text"
        placeholder="Game Name"
        value={gameName}
        onChange={(e) => setGameName(e.target.value)}
        required
      />
      <textarea
        placeholder="Game Description"
        value={gameDescription}
        onChange={(e) => setGameDescription(e.target.value)}
        required
      />
      <button type="submit">Submit Request</button>
    </form>
  );
};

function App() {
  const [currentGame, setCurrentGame] = useState(null);
  const [gameRequests, setGameRequests] = useState([]);

  const games = [
    {
      title: "Slithery Snacker",
      description: "Eat pixels, grow long, don't bite yourself!",
      component: SnakeGame
    },
    {
      title: "Whack-a-Mole",
      description: "Bop those moles as they pop up!",
      component: WhackAMole
    },
    {
      title: "Memory Match",
      description: "Find the pairs and train your brain!",
      component: MemoryMatch
    }
  ];

  const handleGameClick = (game) => {
    setCurrentGame(game);
  };

  const handleBackToHome = () => {
    setCurrentGame(null);
  };

  const handleGameRequest = (request) => {
    setGameRequests([...gameRequests, request]);
    alert(`Thank you for your game request! We've added "${request.name}" to our list of ideas.`);
  };

  return (
    <div className="App">
      <header className="App-header">
        <h1>90s Arcade Revival</h1>
        <p>Experience the pixelated joy of yesterday, today!</p>
      </header>
      {currentGame ? (
        <div className="game-container">
          <button onClick={handleBackToHome} className="back-button">Back to Games</button>
          <currentGame.component />
        </div>
      ) : (
        <main>
          <div className="game-grid">
            {games.map((game, index) => (
              <GameCard 
                key={index}
                title={game.title}
                description={game.description}
                onClick={() => handleGameClick(game)}
              />
            ))}
          </div>
          <GameRequestForm onSubmit={handleGameRequest} />
          {gameRequests.length > 0 && (
            <div className="game-requests">
              <h3>Requested Games</h3>
              <ul>
                {gameRequests.map((request, index) => (
                  <li key={index}>{request.name}: {request.description}</li>
                ))}
              </ul>
            </div>
          )}
        </main>
      )}
      <footer className="App-footer">
        <p>¬© 2024 90s Arcade Revival | Made with üíñ and a lot of üïπÔ∏è</p>
        <p className="blink">Best viewed on a 640x480 CRT monitor</p>
      </footer>
    </div>
  );
}

export default App;