import React, { useState, useEffect } from 'react';

const GAME_DURATION = 30000; // 30 seconds
const MOLE_APPEAR_TIME = 1000; // 1 second

const WhackAMole = () => {
  const [score, setScore] = useState(0);
  const [timeLeft, setTimeLeft] = useState(GAME_DURATION / 1000);
  const [activeMole, setActiveMole] = useState(null);
  const [gameActive, setGameActive] = useState(false);

  useEffect(() => {
    let timer;
    let moleTimer;

    const startGame = () => {
      setScore(0);
      setTimeLeft(GAME_DURATION / 1000);
      setGameActive(true);
      setActiveMole(null);

      timer = setInterval(() => {
        setTimeLeft((prevTime) => {
          if (prevTime <= 1) {
            clearInterval(timer);
            clearTimeout(moleTimer);
            setGameActive(false);
            return 0;
          }
          return prevTime - 1;
        });
      }, 1000);

      const showMole = () => {
        setActiveMole(Math.floor(Math.random() * 9));
        moleTimer = setTimeout(() => {
          setActiveMole(null);
          if (gameActive) showMole();
        }, MOLE_APPEAR_TIME);
      };

      showMole();
    };

    if (gameActive) {
      startGame();
    }

    return () => {
      clearInterval(timer);
      clearTimeout(moleTimer);
    };
  }, [gameActive]);

  const whackMole = (index) => {
    if (index === activeMole) {
      setScore((prevScore) => prevScore + 1);
      setActiveMole(null);
    }
  };

  return (
    <div className="whack-a-mole">
      <h2>Whack-a-Mole</h2>
      <div className="game-info">
        <p>Score: {score}</p>
        <p>Time Left: {timeLeft}s</p>
      </div>
      <div className="game-grid">
        {[...Array(9)].map((_, index) => (
          <div
            key={index}
            className={`mole-hole ${activeMole === index ? 'active' : ''}`}
            onClick={() => whackMole(index)}
          >
            {activeMole === index && <div className="mole" />}
          </div>
        ))}
      </div>
      {!gameActive && (
        <button onClick={() => setGameActive(true)} className="start-button">
          {timeLeft === GAME_DURATION / 1000 ? 'Start Game' : 'Play Again'}
        </button>
      )}
    </div>
  );
};

export default WhackAMole;